import React, { Component } from 'react'
import {data} from './index'
import {db} from './index'

const obj = {id:0,img:'',serving:[],name: ['',''],cal:0,fat:0,carb:0,fiber:0,sugar:0,protein:0,};
let emptyNode = [];
let fetchObj;
function Serving(props) {
    return(
        <>
        <input type='text' placeholder="English Description" onChange={e => obj.serving[props.id].dis[0] = e.target.value}></input>
        <br />
        <input type='text' placeholder="Arabic Description" onChange={e => obj.serving[props.id].dis[1] = e.target.value}></input>
        <br />
        <input type='number' placeholder="value" onChange={e => obj.serving[props.id].value = e.target.value}></input>
        <br />
        <hr />
        </>
    )
    
}
class Secret extends Component {
    state = {render: ''}
    mapServing(Arr) { 
        return (
        Arr.map((e,index)=> <Serving key={index} id={e.id} />)
        )
    }
    fetching = (x) => {
        var docRef = db.collection("App").doc(x)
        docRef.get().then(function(doc) {
            if (doc.exists) {
                fetchObj = doc.data();
                console.log(fetchObj)
            }})
    } 

    render() {
        return(
            <div id='secret'>
                <div id='secretContainer'>
                <p>ID:</p><input type='number' placeholder={data.length} onChange={e => {obj.id = e.target.value; this.fetching(e.target.value)}}></input><br />
                <p>Image Url:</p><input type='url' placeholder="Image Url" onChange={e => obj.img = `url(${e.target.value})`}></input><br />
                <p>Name:</p><input type='text' placeholder="English Name" onChange={e => obj.name[0] = e.target.value}></input><br />
                            <input type='text' placeholder="Arabic Name" onChange={e => obj.name[1] = e.target.value}></input><br />
                <p>Calories:</p><input type='number' placeholder="Calories" onChange={e => obj.cal = e.target.value}></input><br />
                <p>Fat:</p><input type='number' placeholder="Fat" onChange={e => obj.fat = e.target.value}></input><br />
                <p>Carbs:</p><input type='number' placeholder="Carbs" onChange={e => obj.carb = e.target.value}></input><br />
                <p>Fiber:</p><input type='number' placeholder="Fiber" onChange={e => obj.fiber = e.target.value}></input><br />
                <p>Sugar:</p><input type='number' placeholder="Sugar" onChange={e => obj.sugar = e.target.value}></input><br />
                <p>Protein:</p><input type='number' placeholder="Protein" onChange={e => obj.protein = e.target.value}></input><br />
                <p>Serving:</p><button style={{width:'165px'}} onClick={() =>{emptyNode.push({id:emptyNode.length}); obj.serving.push({ dis: ["",""],value:0 });this.setState({render: this.mapServing(emptyNode)})}}>+</button><br />
                               <button style={{width:'165px'}} onClick={() =>{emptyNode.pop(); obj.serving.pop();this.setState({render: this.mapServing(emptyNode)})}}>-</button><br />
                {this.state.render}           
                <button style={{width:'165px'}} onClick={() => db.collection("App").doc(data.length.toString()).set(obj)}>Submit</button>
                <button style={{width:'165px'}} onClick={() => {
                    db.collection("App").doc(obj.id.toString()).delete().then(function() {
                        console.log("Document successfully deleted!");
                    }).catch(function(error) {
                    console.error("Error removing document: ", error);
                    });
                    console.log(obj.id)
                }}>Delete</button>
                </div>              
            </div>
        )
    }
}

export default Secret


